<template>
  <div class="professional-role">
    <div class="professional-role__header">
      <el-tabs v-model="activeName" class="professional-role__header__tabs">
        <el-tab-pane
          v-for="{ label, value } in tabArr"
          :key="value"
          :name="value"
        >
          <span
            slot="label"
            class="professional-role__header__tabs__slot-label"
            >{{ label }}</span
          >
        </el-tab-pane>
      </el-tabs>
      <!--此处隐藏了"查看操作日志"，代码可从git历史中找回-->
    </div>
    <div class="professional-role__body">
      <div class="professional-role__body__handle">
        <el-button
          type="primary"
          size="small"
          :loading="addLoading"
          @click="addMemberByPage"
          >新增成员</el-button
        >
      </div>
      <el-divider />
      <list
        class="professional-role__body__list"
        :user-fd-type="activeName"
        :add-loading="addLoading"
        :pageType="pageType"
      />
    </div>
  </div>
</template>

<script>
import List from './list'
import { systemRole } from '@/utils/dictionary'
export default {
  components: { List },
  data() {
    return {
      addLoading: false,
      activeName: 'doctor',
      tabArr: systemRole('', true),
      addMembers: [],
      members: [],
      loading: false,
      pageType: ''
    }
  },
  watch: {
    activeName() {
      window.localStorage.setItem('activeName', this.activeName)
      this.addMembers = []
    },
    '$route.path'(val) {
      this.getLocalActiveName()
      this.getPageType()
    }
  },
  created() {
    this.getLocalActiveName()
    this.getPageType()
  },
  beforeRouteLeave(to, from, next) {
    if (to.name === 'professionalRoleEdit') {
      if (to.params.id === 'new') {
        to.meta.title = '新增成员'
      } else {
        to.meta.title = '编辑成员'
      }
    }
    next()
  },
  methods: {
    systemRole,
    getPageType() {
      this.pageType = ''
      setTimeout(() => {
        this.pageType =
          this.$route.path.indexOf('consultation') !== -1
            ? 'consult'
            : 'familyDoctor'
      }, 400)
    },
    addMemberByPage() {
      if (this.pageType === 'consult') {
        this.$router.push({
          name: 'singleAskRoleEdit',
          params: { id: 'new', role: this.activeName }
        })
      } else {
        this.$router.push({
          name: 'professionalRoleEdit',
          params: { id: 'new', role: this.activeName }
        })
      }
    },
    getLocalActiveName() {
      if (window.localStorage.getItem('activeName') != null) {
        this.activeName = window.localStorage.getItem('activeName')
          ? window.localStorage.getItem('activeName')
          : 'doctor'
      }
    }
  }
}
</script>

<style lang="scss">
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';

.professional-role {
  &__header {
    .el-tabs {
      &__header {
        margin-bottom: 0;
      }
      &__nav-wrap {
        padding-left: 30px;
        &::after {
          height: 1px;
        }
      }
      &__item {
        padding: 5px 16px;
        height: 50px;
        &.is-active {
          .el-button--text {
            color: #{$activeColor};
          }
        }
      }
    }
  }
}
</style>

<style lang="scss" scoped>
.professional-role {
  &__header {
    display: flex;
    &__tabs {
      flex-grow: 1;
      &__slot-label {
        padding: 0 16px;
      }
      &__see-conlog {
        padding: 0 50px;
      }
    }
  }
  &__body {
    &__handle {
      padding-top: 24px;
      width: 500px;
      &__select_box {
        display: inline-block;
        width: 50%;
      }
    }
  }
}
</style>
