<template>
  <div class="professional-role-list">
    <div class="professional-role-list__header">
      <span>成员姓名</span>&nbsp;&nbsp;
      <el-input
        v-model.trim="searchName"
        class="professional-role-list__header__item professional-role-list__header__search"
        size="small"
        placeholder="请输入成员姓名"
        @keyup.enter.native="handleSearchByName"
      />
      <span v-if="userFdType === 'doctor' || pageType === 'consult'">
        <span>{{ pageType === 'consult' ? '服务' : '' }}科室</span>&nbsp;&nbsp;
        <el-select
          v-model="department"
          placeholder="请选择服务科室"
          size="small"
          class="margin-right-10"
        >
          <el-option
            v-for="item in departmentByPage"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </span>
      <el-button
        class="professional-role-list__header__item"
        type="primary"
        size="small"
        @click="handleSearchByName"
        >搜索</el-button
      >
      <el-button
        class="professional-role-list__header__item"
        type="primary"
        size="small"
        plain
        @click="handleReset"
        >重置</el-button
      >
    </div>
    <div class="professional-role-list__body">
      <div
        v-if="pageType === 'consult' || pageType === 'familyDoctor'"
        class="el-card-warpper professional-role-list__body__list"
      >
        <el-table
          class="professional-role-list__body__list__table pbl-table"
          ref="professionalRoleTable"
          :data="tableData"
        >
          <el-table-column
            align="center"
            label="成员编号"
            min-width="100"
            prop="no"
            fixed
          />
          <el-table-column
            align="center"
            label="成员姓名"
            min-width="70"
            prop="name"
          />
          <el-table-column align="center" label="头像" min-width="80">
            <template slot-scope="scope">
              <el-avatar
                :size="30"
                :key="scope.row._id"
                :src="scope.row.busUserAvatar"
              />
            </template>
          </el-table-column>
          <el-table-column align="center" label="联系方式" min-width="100">
            <template slot="header">
              <span>
                <span> 联系方式</span>
                <el-tooltip
                  class="item color-576277"
                  effect="dark"
                  content="电话转诊等类似业务将会呼叫该联系方式"
                  placement="top"
                >
                  <i class="el-icon-info" />
                </el-tooltip>
              </span>
            </template>
            <template slot-scope="scope">{{ scope.row.mobile }}</template>
          </el-table-column>
          <el-table-column align="center" label="性别" min-width="60">
            <template slot-scope="scope">{{ scope.row.gender }}</template>
          </el-table-column>
          <el-table-column
            v-if="pageType === 'consult'"
            align="center"
            label="问诊配置"
            min-width="160"
          >
            <template slot-scope="scope">
              <span
                v-for="(item, index) in scope.row.single_ask_price_info"
                :key="index"
              >
                {{ sectionOffice(item.department, '', true) }}（￥{{
                  fenToYuan(item.price)
                }}）
                <span v-if="index < scope.row.single_ask_price_info.length - 1"
                  >,</span
                >
              </span>
            </template>
          </el-table-column>
          <el-table-column
            v-if="userFdType !== 'dietician' && pageType !== 'consult'"
            align="center"
            label="科室"
            min-width="100"
          >
            <template slot-scope="scope">{{
              sectionOffice(scope.row.department, '', true)
            }}</template>
          </el-table-column>
          <el-table-column align="center" width="160">
            <template slot="header" slot-scope="">
              <span>{{ serverStatus }}</span>
            </template>
            <template slot-scope="scope">
              <div>
                <el-switch
                  :value="scope.row.server.is_on_server"
                  :width="60"
                  :class="
                    scope.row.server.is_on_server === true
                      ? 'online-style'
                      : 'offline-style'
                  "
                  :active-text="scope.row.server.is_on_server ? '' : ''"
                  :inactive-text="
                    scope.row.server.is_on_server === true ? '上线' : '下线'
                  "
                  inactive-color="#C0C4CC"
                  :disabled="switchBtnStatus"
                  @change="
                    changeOnlineStatusFun(
                      scope.row,
                      scope.row.server.is_on_server
                    )
                  "
                />
              </div>
            </template>
          </el-table-column>
          <el-table-column
            v-if="pageType === 'familyDoctor'"
            align="center"
            label=""
            width="160"
          >
            <template slot="header" slot-scope="">
              <span>服务资源占用情况</span>
              <el-tooltip
                class="item color-576277"
                effect="dark"
                content="当前服务资源占用量/服务上限值"
                placement="top"
              >
                <i class="el-icon-info" />
              </el-tooltip>
            </template>
            <template slot-scope="scope">
              <div>
                {{ scope.row.server.current_num }}<span>/</span
                >{{ scope.row.server.upper_limit }}
              </div>
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            label="操作"
            fixed="right"
            width="160"
          >
            <template slot-scope="scope">
              <div>
                <el-button type="text" @click="handleEdit(scope)"
                  >编辑</el-button
                >
                <el-button type="text" @click="handleDelete(scope)"
                  >删除</el-button
                >
              </div>
            </template>
          </el-table-column>
        </el-table>
        <div style="margin-top: 10px;text-align: center;">
          <el-pagination
            background
            :current-page.sync="page.currentPage"
            :page-sizes="pageSizes"
            :page-size="page.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.total"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
          />
        </div>
      </div>
    </div>
    <tip
      v-if="delTipShow"
      v-model="delTipShow"
      :tip-center="true"
      btn-text="确认删除"
      :has-footer="true"
      :show-cancel-btn="true"
      @btnHandle="btnDelTipSubmitFun"
    >
      <section slot="content" class="center">
        删除后，该成员将从{{
          systemRole(userFdType, '')
        }}列表中移除，确认删除吗？
      </section>
    </tip>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="onlineDialogVisible"
      :before-close="cancelOnlineChangeFun"
      width="30%"
      center
    >
      <span class="align-center">{{ dialogContent }}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelOnlineChangeFun">取 消</el-button>
        <el-button type="primary" @click="confirmChangeOnlineStatus">{{
          dialogConfirmBtnTxt
        }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import {
  getMembers as getProfessionalRoleListApi,
  getSingleAskMembers,
  deleteMember as delProfessionalRoleByIdApi,
  deleteSingleAskMember,
  updateMemberStatus as changeServerStatusApi,
  SingleAskMemberStatus
} from '@/api/professionalRole'
import Tip from '@/components/Dialog'
import Vue from 'vue'
import { fenToYuan } from '@/filters'
import { sectionOffice, systemRole } from '@/utils/dictionary'
const genderFilter = Vue.filter('gender')
export default {
  components: {
    Tip
  },
  props: {
    userFdType: {
      type: String,
      default: 'doctor'
    },
    addLoading: {
      type: Boolean,
      default: false
    },
    pageType: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      departmentByPage: '',
      department: '',
      dialogTitle: '上线提示',
      dialogConfirmBtnTxt: '确认上线',
      dialogContent: '',
      onlineDialogVisible: false,
      onlineTip: '',
      singleAskOnlineTip: '',
      offlineTip: '',
      singleAskOfflineTip: '',
      activeId: '',
      is_on_server: '',
      searchName: '',
      delTipShow: false,
      switchBtnStatus: false,
      tableData: [],
      delId: '',
      page: {
        total: 0,
        pageSize: 10,
        currentPage: 1
      },
      pageSizes: [10, 50, 100],
      serverStatus: ''
    }
  },
  watch: {
    userFdType() {
      this.handleReset()
      this.handleSearch()
    },
    addLoading() {
      this.handleSearch()
    },
    pageType(val, oldVal) {
      this.init()
    }
  },
  created() {
    this.init()
  },
  methods: {
    sectionOffice,
    genderFilter,
    systemRole,
    fenToYuan,

    init() {
      this.pageType = this.pageType
      if (this.pageType) {
        this.serverStatus =
          this.pageType === 'consult' ? '问诊服务状态' : '服务状态'
        this.departmentByPage = this.sectionOffice(
          '',
          'arr',
          this.pageType === 'consult' ? false : true
        )
        this.department = ''
        this.searchName = ''
        this.handleSearch()
      }
    },
    setTip(name) {
      this.onlineTip = '上线后，' + name + '将拥有签约用户的权限，确认上线吗？'
      this.singleAskOnlineTip =
        '上线后，' + name + '将拥有向用户提供单次问诊服务的权限，确认上线吗？'
      this.offlineTip = '下线后，' + name + '将不可以签约用户，确认下线吗？'
      this.singleAskOfflineTip =
        '下线后，用户将无法购买' + name + '的单次问诊服务，确认下线吗？'
    },
    cancelOnlineChangeFun() {
      this.onlineDialogVisible = false
    },
    changeOnlineStatusFun(row, status) {
      this.onlineDialogVisible = true
      this.activeId = row._id
      this.setTip(row.name)
      if (!status || status === 'false') {
        this.is_on_server = true
        this.dialogTitle = '上线提示'
        this.dialogContent =
          this.pageType === 'consult' ? this.singleAskOnlineTip : this.onlineTip
        this.dialogConfirmBtnTxt = '确认上线'
      } else {
        this.is_on_server = false
        this.dialogTitle = '下线提示'
        this.dialogContent =
          this.pageType === 'consult'
            ? this.singleAskOfflineTip
            : this.offlineTip
        this.dialogConfirmBtnTxt = '确认下线'
      }
    },
    confirmChangeOnlineStatus() {
      if (!this.activeId) {
        return
      }
      let params = {}
      if (this.pageType === 'consult') {
        params = { single_ask_status: this.is_on_server }
      } else {
        params = { status: this.is_on_server }
      }
      let apiName =
        this.pageType === 'consult'
          ? SingleAskMemberStatus
          : changeServerStatusApi
      apiName(this.activeId, params).then(res => {
        this.$message.success('修改状态成功')
        this.onlineDialogVisible = false
        this.handleSearch()
      })
    },
    setTableData({ list }) {
      this.tableData = []
      this.tableData = list.map((item, index) => ({
        _id: item._id ? item._id : '',
        id: item.no ? item.no : '',
        name: item.name,
        busUserAvatar: item.avatar ? item.avatar : '',
        gender: item.gender ? genderFilter(item.gender) : '未选择',
        server: {
          is_on_server:
            this.pageType === 'consult'
              ? item.single_ask_status
              : item.is_on_server
              ? item.is_on_server
              : 'false',
          current_num: item.fd_resource_curr ? item.fd_resource_curr : '0',
          upper_limit: item.fd_resource ? item.fd_resource : '0'
        },
        mobile: item.mobile,
        single_ask_price_info: item.single_ask_price_info
          ? item.single_ask_price_info
          : '',
        single_ask_status: item.single_ask_status
          ? item.single_ask_status
          : false,
        department: item.department,
        no: item.no
      }))
    },

    setPage({ page }) {
      this.page.total = page.total
    },
    async handleSearch(pageQuery) {
      const _query = pageQuery || {}
      _query.bus_role = this.userFdType ? this.userFdType : 'doctor'
      _query.pageSize = this.page.pageSize
      _query.currentPage = this.page.currentPage
      if (this.searchName) _query.name = this.searchName
      if (this.department) _query.department = this.department
      if (this.pageType === 'consult') {
        _query.single_ask_bus_role = _query.bus_role
        delete _query.bus_role
        let { data } = await getSingleAskMembers(_query)
        this.setTableData({ ...data })
        this.setPage({ ...data })
      } else {
        let { data } = await getProfessionalRoleListApi(_query)
        this.setTableData({ ...data })
        this.setPage({ ...data })
      }
    },

    handleReset() {
      this.searchName = ''
      this.department = ''
      this.page.pageSize = 10
      this.page.currentPage = 1
      this.handleSearch()
    },

    handleSearchByName() {
      this.page.pageSize = 10
      this.page.currentPage = 1
      this.handleSearch()
    },

    handleEdit({ row }) {
      if (this.pageType === 'consult') {
        this.$router.push({
          name: 'singleAskRoleEdit',
          params: { id: row._id, role: this.userFdType }
        })
      } else {
        this.$router.push({
          name: 'professionalRoleEdit',
          params: { id: row._id, role: this.userFdType }
        })
      }
    },

    handleDelete({ row, $index }) {
      this.delTipShow = true
      this.delId = row._id
    },
    btnDelTipSubmitFun() {
      let deleteApi =
        this.pageType === 'consult'
          ? deleteSingleAskMember
          : delProfessionalRoleByIdApi
      deleteApi(this.delId).then(res => {
        this.$message.success('删除成功')
        this.delTipShow = false
        this.handleSearch()
      })
    },

    async handleCurrentChange(currentPage) {
      this.page.currentPage = currentPage
      this.handleSearch()
    },
    async handleSizeChange(pageSize) {
      this.page.pageSize = pageSize
      this.handleSearch()
    }
  }
}
</script>
<style lang="scss">
.professional-role-list {
  .el-card__header {
    margin-bottom: 16px;
  }
  .el-avatar > img {
    width: 100%;
  }
  &__body {
    &__list {
      .el-card__body {
        height: calc(100% - 72px);
      }
      .color-576277 {
        color: #576277;
      }
    }
  }
  .el-form-item {
    &__label,
    &__content {
      line-height: 32px;
    }
  }
  .el-dialog--center .el-dialog__body {
    text-align: center !important;
  }
}
</style>

<style lang="scss" scoped>
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';

.professional-role-list {
  min-height: 100%;
  &__header {
    &__search {
      margin-right: 30px;
      width: 220px;
    }
  }
  &__body {
    padding-top: 30px;
    height: calc(100% - 32px);
    &__list {
      overflow: auto;
      height: 100%;
      .el-card {
        height: 100%;
      }
      .pbl-table {
        &__name-col {
          display: inline-flex;
          align-items: center;
          line-height: 0;
          &__name {
            padding-right: 10px;
          }
          &__icon {
            font-size: 18px;
            color: #5ba5e8;
            cursor: pointer;
          }
        }
      }
      &__pagination {
        padding: 60px 0 40px;
        text-align: center;
      }
    }
  }
}
</style>
